import { FC, useEffect } from "react";
import { AppConfig, UserSession, showConnect } from "@stacks/connect";
import Button from "./Button"; // Assuming you have a Button component

const appConfig = new AppConfig(['store_write']);

const Login: FC<{
  setWalletAddress: (address: string) => void;
  setUserSession: (session: UserSession) => void;
  setShowDashboard: (state: boolean) => void;
  showDashboard: boolean;
}> = ({ setWalletAddress, setUserSession, setShowDashboard, showDashboard }) => {

  const userSession = new UserSession({ appConfig });

  useEffect(() => {
    if (userSession.isUserSignedIn()) {
      const { testnet } = userSession.loadUserData().profile.stxAddress;
      setWalletAddress(testnet);
      setUserSession(userSession);
    }
  }, [setWalletAddress, setUserSession]);

  const connectWallet = () => {
    showConnect({
      appDetails: { name: "StacksVote", icon: "../projectLogo.jpg" },
      onFinish: () => {
        setWalletAddress(userSession.loadUserData().profile.stxAddress.testnet);
        setUserSession(userSession);
        window.location.reload();
      },
      userSession,
    });
  };

  const disconnectWallet = () => {
    if (userSession.isUserSignedIn()) {
      userSession.signUserOut(window.location.href);
      setWalletAddress('');
      setUserSession(null);
    }
  };

  return (
    <div style={headerStyle}>
      <div style={walletBoxStyle}>
        <span style={{ marginRight: '10px' }}>Wallet Address:</span>
        {userSession.isUserSignedIn() ? (
          <span>{userSession.loadUserData().profile.stxAddress.testnet}</span>
        ) : (
          <span>No conectado</span>
        )}
      </div>
      <div>
        <button style={navButtonStyle} onClick={() => setShowDashboard(!showDashboard)}> {showDashboard ? "Dashboard" : "Vote" } </button>
        {userSession.isUserSignedIn() ? (
          <Button label="Disconnect Wallet" onClick={disconnectWallet} />
        ) : (
          <Button label="Connect Wallet" onClick={connectWallet} />
        )}
      </div>
    </div>
  );
};

export default Login;

// CSS styles in JS
const headerStyle = {
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  backgroundColor: '#f8f9fa',
  padding: '10px 20px',
  borderBottom: '2px solid #dee2e6',
};

const walletBoxStyle = {
  padding: '10px',
  backgroundColor: '#e9ecef',
  borderRadius: '5px',
  fontWeight: 'bold',
};

const navButtonStyle = {
  backgroundColor: '#007BFF',
  color: '#fff',
  padding: '10px 20px',
  border: 'none',
  borderRadius: '5px',
  cursor: 'pointer',
  marginRight: '10px',
};
