import { UserSession, showConnect } from '@stacks/connect';
import './App.css';
import Login from "./components/Login";
import Vote from "./components/Vote";
import React, { useState, useEffect } from 'react';
import Dashborad from './components/Dashboard';


function App() {
  const [passCandidates, setPassCandidates] = useState([]);
  const [showDashboard, setShowDashboard] = useState(false);
  const [walletAddress, setWalletAddress] = useState('');
  const [userSession, setUserSession] = useState();
  return (
    <div>
      <Login setWalletAddress={setWalletAddress} setUserSession={setUserSession} setShowDashboard={setShowDashboard} showDashboard={showDashboard} />
      {
        showDashboard ? <Dashborad walletAddress={walletAddress} userSession={userSession} showDashboard={showDashboard} setDashboard={setShowDashboard} passCandidates={passCandidates}/> : <Vote walletAddress={walletAddress} userSession={userSession} setPassCandidates={setPassCandidates}/>
      }
    </div>
  );
}


export default App;
